<template>
  <a-row type="flex"  justify="end" style="min-width: 1400px">
    <a-col style="width: 85%" >
      <div :style="{ background: '#fff', padding: '24px', minHeight: '280px' }">
        <a-row :gutter="24" type="flex" justify="center">
          <a-col :span="3">
            <a-card :bordered="false">
              <div class="account-center-avatarHolder">
                <div class="avatar">
                  <img src="../../assets/dddb-webEnt/company1.png" />
                </div>
              </div>
            </a-card>
          </a-col>
          <a-col :span="17">
            <a-form id="components-form-demo-validate-other" :form="form">
              <a-form-item label="用户名" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{entInfo.enterName}}</span>
              </a-form-item>
              <a-form-item label="在建工程名称" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{entInfo.nowProject}}</span>
              </a-form-item>
              <a-form-item label="经营范围" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <span class="ant-form-text">{{entInfo.scope}}</span>
              </a-form-item>
              <a-form-item label="企业介绍" :labelCol="{ span: 6 }" :wrapperCol="{ span: 14 }">
                <p class="ant-form-text">{{entInfo.enterDes}}</p>
              </a-form-item>
            </a-form>
          </a-col>
        </a-row>
        <a-divider />
        <a-row type="flex" justify="center">
          <a-col :span="3">
            <h2>企业资质信息</h2>
          </a-col>
          <a-col :span="3" offset="12">
            <!-- <a href="#" @click="exportExcel"><a-icon type="download" />一键下载企业资质</a> -->
          </a-col>
        </a-row>
        <a-divider>
          <h3 style="width:124px;
            line-height: 40px;
            color: white;
            height:42px;
            background:rgba(184,12,10,1);
            box-shadow:0px 2px 20px 0px rgba(171,171,171,0.5);
            border-radius:21px;">
            必填
          </h3>
        </a-divider>
        <a-row type="flex" justify="start">
          <a-col :span="6" offset="3">
            <h2>企业名称:{{entInfo.enterName}}</h2>
          </a-col>
        </a-row>
        <a-row type="flex" justify="center">
          <a-col :span="18">
            <div class="card-list" ref="content">
              <a-list rowKey="id" :grid="{gutter: 96, column: 3}" :dataSource="requiredData">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-card :hoverable="true" @click="handlePreview(item.sourceType,item.imgSource,item.otherFileSource)">
                      <img  alt="example" v-bind:src="item.imgSource" slot="cover" />
                      <template class="ant-card-actions" slot="actions">
                        <a>{{item.name}}</a>
                      </template>
                    </a-card>
                  </template>
                </a-list-item>
              </a-list>
            </div>
          </a-col>
        </a-row>
        <a-divider>
          <h3 style="width:124px;
            line-height: 40px;
            color: white;
            height:42px;
            background:rgba(184,12,10,1);
            box-shadow:0px 2px 20px 0px rgba(171,171,171,0.5);
            border-radius:21px;">
            选填
          </h3>
        </a-divider>
        <a-row type="flex" justify="center">
          <a-col :span="18">

            <div class="card-list" ref="content">
              <a-list rowKey="id" :grid="{gutter: 96, column: 3}" :dataSource="selectedData">
                <a-list-item slot="renderItem" slot-scope="item">
                  <template>
                    <a-card :hoverable="true" style="position: relative;" @click="handlePreview(item.sourceType,item.imgSource,item.otherFileSource)">
                      <img v-bind:alt="item.name" v-bind:src="item.imgSource" slot="cover" />
                      <!-- <span style="position: absolute; top: 60px; left: 100px; padding: 0; margin: 0; color: #FFFFFF ">添加文字</span> -->
                      <template class="ant-card-actions" slot="actions">
                        <a>{{item.name}}</a>
                      </template>
                    </a-card>
                  </template>
                </a-list-item>
              </a-list>
              <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
                <img alt="example" style="width: 100%" :src="previewImage" />
              </a-modal>
            </div>
          </a-col>
        </a-row>
        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>

<!--      审核吸底-->
      <a-affix v-if="isPassed" :offsetBottom="0">
        <div class="affix-bottom">
          <a-form layout="inline" @submit="handleSubmit" :form="form">
              <a-form-item label="反馈意见" >
                <a-input
                  style="width: 500%"
                  placeholder="请输入反馈意见"
                  name="authAdvice"
                  v-decorator="[
            'authAdvice',
            {rules: [{ required: false, message: '请输入需求名称' }]}
          ]"
                />
              </a-form-item>
              <a-form-item style="text-align: center ;margin-left: 700px">
                <a-button htmlType="submit" type="primary" @click="postAuthResult('未通过')">驳回</a-button>
              </a-form-item>
              <a-form-item style="text-align: center ">
                <a-button htmlType="submit" type="primary" @click="postAuthResult('已通过')">通过</a-button>
              </a-form-item>
          </a-form>
        </div>
    </a-affix>
    </a-col>

  </a-row>
</template>
<script>
import {getEnterpriseInfoById,postEnterAuthResult} from '../../api/manRequest'
import globle from '../../api/global_varialble'

export default {
  components: {},
  data() {
    return {
      isPassed: true,
      previewVisible: false,
      previewImage: '',
      imgUrl: globle.baseURL,
      entId: Number(this.$route.query.id),
      entInfo:{},
      entAuthState:'',
      form: this.$form.createForm(this)
    };
  },
  computed: {
    // entProfileInfo:[
    //     {
    //       index: this.entProfileInfo.id,
    //       enterName: this.entProfileInfo.enterName,
    //       businessLicense: this.imgUrl + this.entProfileInfo.businessLicense,
    //       constructionQUAL: this.imgUrl + this.entProfileInfo.constructionQUAL,
    //       securityLicense: this.imgUrl + this.entProfileInfo.securityLicense,
    //       socialSecurityCert: this.imgUrl + this.entProfileInfo.socialSecurityCert,
    //       noticeOfBid: this.imgUrl + this.entProfileInfo.noticeOfBid,
    //       businessItemInsurance: this.imgUrl + this.entProfileInfo.businessItemInsurance,
    //       noTaxExpStatement: this.imgUrl + this.entProfileInfo.noTaxExpStatement,
    //       planningPermit: this.imgUrl + this.entProfileInfo.planningPermit,
    //       constructionPermit: this.imgUrl + this.entProfileInfo.constructionPermit,
    //       landUseCert: this.imgUrl + this.entProfileInfo.landUseCert,
    //       startReport: this.imgUrl + this.entProfileInfo.startReport
    //     }
    //   ],
    requiredData(){
      let list = [
        {
          name: "营业执照",
          link: this.entInfo.businessLicense,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.businessLicense
        },
        {
          name: "建筑资质",
          link:this.entInfo.constructionQUAL,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.constructionQUAL
        },
        {
          name: "安全生产许可证",
          link:this.entInfo.securityLicense,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.securityLicense
        },
        {
          name: "社保缴费证明",
          link:this.entInfo.socialSecurityCert,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.socialSecurityCert
        },
        {
          name: "拟用工项目中标通知书或其他文件",
          link:this.entInfo.noticeOfBid,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.noticeOfBid
        },
        {
          name: "项目商业保险",
          link:this.entInfo.businessItemInsurance,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.businessItemInsurance
        },
        {
          name: "无纳税异常声明",
          link:this.entInfo.noTaxExpStatement,
          sourceType: "",
          otherFileSource: '',
          imgSource: this.imgUrl+ this.entInfo.noTaxExpStatement
        }
      ];
      for (let index = 0; index < list.length; index++) {
        const element = list[index];
        if (element.link == null) {
          element.imgSource = 'static/D-qualification-cardbg.png'
          element.sourceType = "pic"
        }
        else if (element.link != null) {
        var imgLink = (element.link).toString()
          if (imgLink.indexOf(".pdf") != -1) {
            element.otherFileSource = this.imgUrl + element.link
            element.imgSource = 'static/D-qualification-uploaded.jpeg'
            element.sourceType = "other"
          }else {
            element.imgSource = this.imgUrl+element.link;
          }
        }
      }
      return list;
    },
    selectedData(){
      let list = [
        {
          name: "土地使用证",
          link:this.entInfo.landUseCert,
          imgSource: this.imgUrl+ this.entInfo.landUseCert
        },
        {
          name: "规划许可证",
          link:this.entInfo.planningPermit,
          imgSource: this.imgUrl+ this.entInfo.planningPermit
        },
        {
          name: "施工许可证",
          link:this.entInfo.constructionPermit,
          imgSource: this.imgUrl+ this.entInfo.constructionPermit
        },
        {
          name: "开工报告",
          link:this.entInfo.startReport,
          imgSource: this.imgUrl+ this.entInfo.startReport
        }
      ];
      for (let index = 0; index < list.length; index++) {
        const element = list[index];
        var imgLink = toString(element.link)
        if (element.link == null) {
          element.imgSource = 'static/D-qualification-cardbg.png'
        }
        else if (element.link != null) {
          if (imgLink.indexOf(".pdf") != -1) {
            element.imgSource = 'static/D-qualification-cardbg.png'            
          }else {
            element.imgSource = this.imgUrl+element.link;
          }
        }
      }
      return list;
    }
  },
  mounted() {
    this.getEnterpriseInfoById();
  },
  created() {
    this.$parent.showSiderMenu = true;
    this.$parent.showMenu = false;
    // console.log(this.entId);
  },
  methods: {
    // 图片预览
    handleCancel() {
        this.previewVisible = false;
      },
      handlePreview(fileType,imgSrc,otherSrc) {
        if (fileType === "other") {
          window.open(otherSrc)
        }
        else {
          // console.log(imgSrc)
        this.previewImage = imgSrc;
        this.previewVisible = true;
        }
      },
    postAuthResult(isPass){
      const {
        form: { validateFields }
      } = this;
      
      // 先校验，通过表单校验后，才进入下一步
      validateFields((err, values) => {
        if (!err) {
          if (isPass ==='已通过' && values.authAdvice ===undefined) {
            values.authAdvice = ''
          }
          if (isPass ==='未通过' && values.authAdvice ===undefined) {
            this.error()
          }
          const postAuthInfo = { ...values,id:this.entId,authState:isPass };
          // console.log(values.authAdvice);
          const that = this;
          postEnterAuthResult(postAuthInfo)
            .then(function(response) {
              // console.log(response);
              if (response.data.result === 25) {
                alert("审核成功");
                that.$router.push({ path: "/entlist" });
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        }
      });
    },
    getEnterpriseInfoById(){
      getEnterpriseInfoById(this.entId)
      .then(response => {
        this.entInfo = response.data.data;
        this.entAuthState = response.data.data.authState;
        // this.isPassedShow();
        this.thisimgSource = response.data.data.businessLicense;
      })
      .catch(err => {
        console.log(err);
      })
    },
    isPassedShow(){
      // console.log(this.entAuthState)
      if (this.entAuthState === '审核中') {
        this.isPassed = true
      }else{
        this.isPassed = false
      }
    },
    // 下载
    exportExcel() {
            import('../../excle/Export2Excel').then(excel => { // 导入js模块
                const tHeader = ['企业ID', '企业名称', '营业执照', '建筑资质','安全生产许可证','社保缴费证明','拟用工项目中标通知书或其他文件','项目商业保险','无纳税异常声明','规划许可证','施工许可证','土地使用证','开工报告']; // 导出excel 的标题
                const filterVal = ['id','enterName', 'businessLicense', 'constructionQUAL','securityLicense','socialSecurityCert','noticeOfBid','businessItemInsurance','noTaxExpStatement','planningPermit','constructionPermit','landUseCert','startReport']; // 每个标题对应的字段

                const list = (this.entProfileInfo || []).map((item, key) => { // 通过 map 方法遍历，组装数据成上面的格式
                    return {
                        id: item.id,
                        enterName: item.enterName,
                        businessLicense: this.imgUrl + item.businessLicense,
                        constructionQUAL: this.imgUrl + item.constructionQUAL,
                        securityLicense: this.imgUrl + item.securityLicense,
                        socialSecurityCert: this.imgUrl + item.socialSecurityCert,
                        noticeOfBid: this.imgUrl + item.noticeOfBid,
                        businessItemInsurance: this.imgUrl + item.businessItemInsurance,
                        noTaxExpStatement: this.imgUrl + item.noTaxExpStatement,
                        planningPermit: this.imgUrl + item.planningPermit,
                        constructionPermit: this.imgUrl + item.constructionPermit,
                        landUseCert: this.imgUrl + item.landUseCert,
                        startReport: this.imgUrl + item.startReport
                    };
                });

                if (list) {
                    const data = this.formatJson(filterVal, list); // 生成json数据
                    excel.export_json_to_excel( // 调用excel方法生成表格
                        tHeader,
                        data,
                        this.entInfo.enterName + '的企业资质'
                    );
                } else {
                    alert('暂无无数据');
                }
                // this.loading = false;
            })
        },
        formatJson (filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },
    error() {
       this.$error({
          title: '请输入反馈意见',
          content: '驳回企业审核信息时需要输入反馈意见',
        }); 
      },
  }
};
</script>

<style lang="less" scoped>
.account-center-avatarHolder {
  text-align: center;
  margin-bottom: 24px;

  & > .avatar {
    margin: 0 auto;
    width: 104px;
    height: 104px;
    margin-bottom: 20px;
    border-radius: 50%;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
    }
  }

  .username {
    color: rgba(0, 0, 0, 0.85);
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
    margin-bottom: 4px;
  }
}

.account-center-detail {
  p {
    margin-bottom: 8px;
    padding-left: 26px;
    position: relative;
  }
}

.card-list {
  /deep/ .ant-card-body:hover {
    .ant-card-meta-title > a {
      color: #f5222d;
    }
  }

  /deep/ .ant-card-meta-title {
    margin-bottom: 12px;

    & > a {
      display: inline-block;
      max-width: 100%;
      color: rgba(0, 0, 0, 0.85);
    }
  }

  /deep/ .meta-content {
    position: relative;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    height: 64px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;

    margin-bottom: 1em;
  }
}

.card-avatar {
  width: 48px;
  height: 48px;
  border-radius: 48px;
}
.affix-bottom {
  background: #B80C0A;
  width: 100%;
  margin: 0 auto;
  height: 80px;
  padding: 20px 0 0 20px;
}
.ant-card-actions {
  background: #f7f9fa;

  li {
    float: left;
    text-align: center;
    margin: 12px 0;
    color: rgba(0, 0, 0, 0.45);
    width: 50%;

    &:not(:last-child) {
      border-right: 1px solid #e8e8e8;
    }

    a {
      color: rgba(0, 0, 0, 0.45);
      line-height: 22px;
      display: inline-block;
      width: 100%;
      &:hover {
        color: #f5222d;
      }
    }
  }
}

.new-btn {
  background-color: #fff;
  border-radius: 2px;
  width: 100%;
  height: 188px;
}

.vcenter {
  position: absolute;
  left: 50%;
  top: 50%;
}
</style>



